  position: relative;
  margin-top: 48px;
  height: 418px;
}

.card-CV__avatar {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  width: 96px;
  height: 96px;
}

.card-CV__info {
  padding-top: 56px;
}

.scrollbar-none::-webkit-scrollbar {
  display: none;
}

.scrollbar-style::-webkit-scrollbar {
  height: .3rem;
  border-radius: .3rem;
}

